import React, { useState } from 'react';
import Image from 'next/image'; // Import Image from Next.js
import { assets } from '@/assets/assets'; // Adjust the path to where your assets are stored

const Contact = () => {
  const [result, setResult] = useState("");

  const onSubmit = async (event) => {
    event.preventDefault();
    setResult("Sending...");
    
    const formData = new FormData(event.target);
    formData.append("access_key", "95884b14-0be0-434a-aa65-d5f48d9770a7");

    const response = await fetch("https://api.web3forms.com/submit", {
      method: "POST",
      body: formData
    });

    const data = await response.json();

    if (data.success) {
      setResult("Form Submitted Successfully!");
      event.target.reset();
    } else {
      console.log("Error", data);
      setResult(data.message);
    }
  };

  return (
    <div 
      id="contact" 
      className="w-full px-10 lg:px-[12%] py-16 scroll-mt-20 bg-[url('/footer-bg-color.png')] bg-cover bg-center"
    >
      <h4 className="text-center mb-2 text-lg font-Ovo text-gray-700">Connect with Me</h4>
      <h2 className="text-center text-5xl font-Ovo text-black">Get in Touch</h2>

      <p className="text-center max-w-2xl mx-auto mt-5 mb-12 font-Ovo text-gray-600">
        I specialize in delivering high-quality development services tailored to your needs.
      </p>

      <form onSubmit={onSubmit} className="max-w-2xl mx-auto">
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6 mt-10 mb-8">
          <input 
            type="text" 
            name="name" 
            placeholder="Enter your name" 
            required 
            className="w-full p-3 outline-none border border-gray-400 rounded-md bg-white"
          />
          <input 
            type="email" 
            name="email"
            placeholder="Enter your email" 
            required 
            className="w-full p-3 outline-none border border-gray-400 rounded-md bg-white"
          />
        </div>
        <textarea 
          rows="6" 
          name="message"
          placeholder="Enter your message" 
          required 
          className="w-full p-4 outline-none border border-gray-400 rounded-md bg-white mb-6"
        ></textarea>
        <button 
          type="submit" 
          className="py-3 px-8 w-max flex items-center justify-between gap-2 bg-black/80 text-white rounded-full mx-auto hover:bg-black duration-500"
        >
          Submit Now 
          <Image src={assets.right_arrow_white} alt="Arrow" width={16} height={16} />
        </button>

        <p className="text-center mt-4 text-gray-700">{result}</p>
      </form>
    </div>
  );
};

export default Contact;
