import React, { useState, useEffect, useRef } from 'react';
import Image from 'next/image';
import { assets } from '@/assets/assets';

const Navbar = () => {
  const [isScroll, setIsScroll] = useState(false);
  const sideMenuRef = useRef(null);

  const openMenu = () => {
    if (sideMenuRef.current) {
      sideMenuRef.current.style.transform = 'translateX(0)';
    }
  };

  const closeMenu = () => {
    if (sideMenuRef.current) {
      sideMenuRef.current.style.transform = 'translateX(-100%)';
    }
  };

  useEffect(() => {
    const handleScroll = () => {
      setIsScroll(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <>
      {/* Background Image Section */}
      <div className="fixed top-0 w-11/12 -z-10 translate-y-[-80%]">
        <Image src={assets.header_bg_color} alt="Background" className="w-full" />
      </div>

      {/* Navbar */}
      <nav className="w-full fixed px-5 lg:px-8 xl:px-[8%] py-4 flex items-center justify-between z-50">
        {/* Logo (Outside Navbar) */}
        <a href="#top" className="ml-6">
          <Image src={assets.logo} alt="Logo" width={100} height={25} className="cursor-pointer" />
        </a>

        {/* Centered Menu (Pill-Shaped) */}
        <div
          className={`fixed left-1/2 transform -translate-x-1/2 px-6 lg:px-8 py-1 flex items-center z-50 transition-all duration-300
          rounded-full backdrop-blur-md ${isScroll ? 'bg-gray-300/60 shadow-lg' : 'bg-gray-200/40 shadow-md'}`}
        >
          <ul className="hidden md:flex items-center gap-6 lg:gap-8 rounded-full px-12 py-3">
            <li><a className="font-Ovo text-gray-700 hover:text-black transition" href="#top">Home</a></li>
            <li><a className="font-Ovo text-gray-700 hover:text-black transition" href="#about">About</a></li>
            <li><a className="font-Ovo text-gray-700 hover:text-black transition" href="#services">Services</a></li>
            <li><a className="font-Ovo text-gray-700 hover:text-black transition" href="#work">My Work</a></li>
            <li><a className="font-Ovo text-gray-700 hover:text-black transition" href="#contact">Contact Me</a></li>
          </ul>
        </div>

        <div className="flex items-center gap-4">
          {/* Dark Mode Toggle Button */}
          <button>
            <Image src={assets.moon_icon} alt="Dark Mode" width={24} height={24} className="w-6" />
          </button>

          {/* Contact Button */}
          <a href="#contact" className="hidden lg:flex items-center gap-3 px-10 py-2.5 border border-gray-500 rounded-full ml-4 font-Ovo">
            Contact
            <Image src={assets.arrow_icon} alt="Arrow" width={12} height={12} className="w-3" />
          </a>

          {/* Mobile Menu Button */}
          <button className="block md:hidden ml-3" onClick={openMenu}>
            <Image src={assets.menu_black} alt="Menu" width={24} height={24} className="w-6" />
          </button>
        </div>
      </nav>

      {/* Mobile Navigation Menu */}
      <ul
        ref={sideMenuRef}
        className="flex md:hidden flex-col gap-4 py-20 px-10 fixed right-[-100%] top-0 bottom-0 w-64 z-50 h-screen bg-rose-50 transition-transform duration-500"
      >
        <div className="absolute top-6 right-6 cursor-pointer" onClick={closeMenu}>
          <Image src={assets.close_black} alt="Close Menu" className="w-5" />
        </div>
        <li><a className="font-Ovo" onClick={closeMenu} href="#top">Home</a></li>
        <li><a className="font-Ovo" onClick={closeMenu} href="#about">About</a></li>
        <li><a className="font-Ovo" onClick={closeMenu} href="#services">Services</a></li>
        <li><a className="font-Ovo" onClick={closeMenu} href="#work">My Work</a></li>
        <li><a className="font-Ovo" onClick={closeMenu} href="#contact">Contact Me</a></li>
      </ul>
    </>
  );
};

export default Navbar;
